<template>
  <div class="container" id="app">
    <h4 class="center  green-text text-darken-1">Welcome</h4>
    <hr>
    <form @submit.prevent="startChat()">
          <div class="input-field">
            <i class="material-icons prefix">account_circle</i>
            <input type="text" class="icon_prefix" placeholder="Your name..." name="name"
            v-model="name" @input="validate()"
            >
          </div>
         <input type="submit" value="Ready" class="btn green lighten-1">
         <input v-if="isAdmin" type="button" value="Delete Data" class="btn red lighten-1"  @click="deleteAllData()" >
    </form>
  </div>
</template>

<script>
import db from '../firebase/init';
import util from '../utils/util'

/**
 * Creatd By @author Robert Aslanyan
 * May 2018 
 *  
 */

export default {
  name:'app',
  data(){
    return{
      name:null,
      isAdmin:false,    
    }
  },
  firestore(){
    return{
      chats:db.collection('chats')
    }
  },
  methods:{
    validate(){

      if(this.name.toUpperCase() === 'Robert'.toUpperCase())
           this.isAdmin = true;

      return  util.validate(this.name);
      
    },
    startChat(){
      if(this.name && this.validate()){ 
        util.setCookie(this.name); // Set Cookie and put in it value of name 
        this.$router.push({ name:'chat' , params:{ name:this.name } }); 
      }
      else
        util.Handler('Enter your name before start chating');
      
        
    },
    /**
     * Delete all data from db and also
     * check if db empty or not
     * @method 
     */
    deleteAllData(){
       return  util.deleteAllData(this.chats);
    },
  },
}
</script>

<style lang="scss" scoped>

.input-field:hover .prefix{
  transition: .3s all;
  transform:translateY(10%);
  color: #26a69a;
}

#app{
  margin: calc(20% - 10%) auto;
  max-width: 500px;
  input[type="submit"]{width: 70%;}
}

</style>



