<template>
  <div class="main">
    <form @submit.prevent="addMessage()">
        
        <div class="input-field flex-input">
            <input type="text" placeholder="Your Message..." class="main_input" 
                   v-model="text" @focus="scroll" @input="textLength"
                   @dblclick="disabled != disabled " :disabled="disabled"
                   >
            <emoji/>
        </div>
        <div class="btn-group">
            <button :type="!disabled ? 'submit' : 'button' " 
            class="submit btn green button" @click="disabled=false">
                 {{ !disabled ? 'Send message' : 'change text'}}
                <i class="material-icons prefix" v-if="!disabled">send</i>
                <span class="max-text">{{ length ? length : 0 }}/100</span>
            </button>
            <button type="button" class="delete btn red button" 
                    :disabled="issetMessage == 0 ? true : false"
                    @click="deleteAllData()" >Delete message
                <i class="material-icons prefix">delete</i>
            </button>
        </div>
       
    </form>
    
  </div>
</template>

<script>

import db from '../firebase/init.js'
import util from '../utils/util';
import Emoji from '../components/Emoji'

/**
 * Creatd By @author Robert Aslanyan
 * May 2018 
 *  
 */

export default { 
    props:['name' , 'content' , 'issetMessage'],
    components:{
        Emoji
    },
    data(){
        return{
            text:null,
            maxTextLenght:100,
            disabled:false,
        }
    },
    computed:{
        length(){
            if(this.text)
                return this.text.length;
        }
    },
    methods:{
        scroll(){   
            return util.scrollBottom(this.content);
        },
        textLength(){  

            if((this.text && this.text.length <= this.maxTextLenght))
                return this.text.length;
            else
                this.disabled = true;
               
        },
        addMessage(){

            let regEx =  /^[^\s]+/ ,   // /^_?[a-z\.]\D|\d+?$/i,
                input = document.querySelector('.main_input');
                this.text = input.value === '' ? this.text : input.value;
                
            let _obj  = {
                text:this.text,
                name:this.name,
                createdAt:Date.now(),
                date:util.getNow()
            }       

            
            if(this.textLength(input) && regEx.test(this.text)){
        
                db.collection('chats').add(_obj);
                this.scroll();
                this.text = '';
            }
            else
                util.Handler('Invalid Value:Field must not be empty , and can`t start and end with space!');
            
        },
        deleteAllData(){

          let delete_message = db.collection('chats').where('name','==', this.name);
     

          delete_message.get().then(function(querySnapshot) {
              
            querySnapshot.forEach(function(doc) {
                doc.ref.delete();
                 return util.Handler('Your messages was deleted !' , false);
            });

          });
            
        },
    
    },

 
}
</script>

<style lang="scss" >
.flex-input{
    display: flex;
}
::-webkit-scrollbar {
    width: 10px;
}
 
::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px rgba(0,0,0,0.3); 
    border-radius: 10px;
}
 
::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background:#d1c4e9; 
}

.btn-group{
    flex-wrap: wrap;
    display: flex;
    min-width: 100%;
    margin-top: 0.5rem;
    .submit{flex-basis: 70%;}
    .delete{flex-basis: 30%;}
    .submit , .delete{
         position: relative;
        .prefix{top: 0; font-size: 1.5rem;}
    }
}
.max-text{
    position: absolute;
    right: 15px;
}


</style>



